const overScan = 150;

export const isElementOnDisplay = (
  element: HTMLElement,
  visibleCallBack?: () => void,
  inVisibleCallBack?: () => void,
) => {
  const { offsetTop, offsetHeight, offsetLeft, offsetWidth } = element;
  const { innerHeight, scrollY, innerWidth } = window;

  const top = offsetTop,
    bottom = offsetTop + offsetHeight,
    left = offsetLeft,
    right = offsetLeft + offsetWidth;

  if (
    top < scrollY + overScan + innerHeight &&
    bottom + overScan > scrollY &&
    left >= 0 &&
    right <= innerWidth
  ) {
    visibleCallBack && visibleCallBack();
  } else {
    inVisibleCallBack && inVisibleCallBack();
  }
};
