const robotsList =
  "Lighthouse|bot|spider|crawl|APIs-Google|AdsBot|Googlebot|Insights|mediapartners|Google Favicon|FeedFetcher|Google-Read-Aloud|DuplexWeb-Google|googleweblight|bing|yandex|baidu|duckduck|yahoo|ecosia|ia_archiver|facebook|instagram|pinterest|reddit|slack|twitter|whatsapp|youtube|semrush";
var reg = new RegExp(robotsList, "i");

export const detectRobots = () => {
  var userAgent = navigator.userAgent;
  return reg.test(userAgent);
};
