export const generateSeed = (length: number = 64) => {
  const availableChars =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789"; // latin alphabet and numbers
  let randomString = "";

  for (let index = 0; index < length; index++) {
    randomString += availableChars.charAt(
      Math.floor(Math.random() * availableChars.length),
    );
  }

  return randomString;
};
