import { User } from "providers/User/Context";
import { ErrorTags, LoggerPayLoad } from "./type";

import type * as SentryType from "@sentry/browser";
import type { BrowserTracing } from "@sentry/tracing";
import { createIntervals } from "utils/createInterval";

const isDevelopment = process.env.NODE_ENV === "development";

class CustomError extends Error {
  constructor(message: any) {
    super(message);
    const errorName = String(message).slice(0, 25);
    this.name = errorName;
  }
}

class Logger {
  Sentry: typeof SentryType | null = null;
  Tracing: typeof BrowserTracing | null = null;

  async init() {
    const Sentry = await import("@sentry/browser");
    const { BrowserTracing } = await import("@sentry/tracing");
    this.Tracing = BrowserTracing;

    this.Sentry = Sentry;
    const traced = new BrowserTracing({});

    Sentry.init({
      dsn: "https://5005b8b2dcc14f1b8d60839fa046d5f5@o1306507.ingest.sentry.io/6549206",
      integrations: [traced],
      tracesSampleRate: 1,
    });
  }

  isInit() {
    return !!this.Sentry;
  }

  async send(sendData: LoggerPayLoad) {
    const { errorTrace, custom, message, place } = sendData;
    try {
      const sendError = () => {
        const { user_email, user_id, user_name } = this.identify();

        this.Sentry?.captureException(new CustomError(errorTrace), {
          user: {
            email: user_email,
            id: user_id,
            username: user_name,
          },
          extra: {
            custom,
            message,
            place,
          },
        });
      };
      if (this.Sentry) {
        sendError();
      } else {
        const { clearIntervals, pushInterval } = createIntervals();
        let tryAmount = 0;

        pushInterval(() => {
          tryAmount++;

          if (this.Sentry) {
            sendError();
            clearIntervals();
          } else if (tryAmount > 15) {
            console.error(">> Error on create Sentry ", this.Sentry, sendData);
          }
        }, 1000);
      }
    } catch (error) {
      console.error(">> Error on errorTracker send: ", error);
    }
  }

  identify() {
    let user: User = {
      user_id: "null",
      user_name: "unLogged",
      user_email: "unLogged",
      avatar: "",
      eligible_daily_wheel: true,
    };

    try {
      const newUser: User | null = JSON.parse(
        localStorage.getItem("user") || "null",
      );
      if (newUser?.user_name) user = newUser;
    } catch {}

    return user;
  }

  error(error: Error | any, tags?: ErrorTags) {
    const { custom, place } = tags || {};

    try {
      this.send({
        errorTrace: error,
        message: `${error}`.slice(0, 25),
        custom,
        place,
      });
    } catch (error) {
      console.error(">> Error on errorTracker error: ", error);
    }
  }
}

const errorLogger = new Logger();

export const track = () => {
  try {
    if (isDevelopment) {
      return;
    }

    const trace = new Error().stack;

    if (!errorLogger.isInit()) {
      errorLogger.init();
    }

    return {
      errors: (err: any, tags?: ErrorTags) => {
        try {
          const typeError = isDevelopment ? "~~~~Error on DEV~~~~ " : "--";
          if (typeof err === "string") {
            err = typeError + err + trace;
          } else if (!err) {
            err = typeError + trace;
          } else if (typeof err === "object" && err.message && err.stack) {
            err = JSON.stringify({
              message: err.message,
              stack: err.stack,
              ...err,
            });
          }

          errorLogger.error(err, tags);
        } catch (error) {
          console.error(">> Error on errorTracker on errors(): ", error);
        }
      },
    };
  } catch (error) {
    console.error(">> Error on errorTracker: ", error);
  }
};

//@ts-ignore
window.createCustomError = (msg = "some custom error", tags?: ErrorTags) => {
  track()?.errors(msg, tags);
};
